import { Group, SpotLight, PointLight, AmbientLight, HemisphereLight, Color, DirectionalLight } from 'three';

export default class BasicLights extends Group {
  constructor(...args) {
    super(...args);

    const point = new PointLight(0xFFFFFF, 1, 10, 1);
    const dir = new DirectionalLight(0xFFFFFF, 0.81);
    const ambi = new AmbientLight( 0x404040 , 0.66);
    const hemi = new HemisphereLight( 0xffffbb, 0x080820, 0.5 );

    dir.position.set(4.865, 10.331, 2.446);
    dir.target.position.set(0,0,0);
    dir.castShadow = true;

    point.position.set(0, 1, 5);

    this.add(ambi, hemi, dir);

  }
}
